import React from 'react'
import listWrapperStyles from './listWrapper.module.scss'
// typeof window !== 'undefined' && window && 
// var sr = require('../ScrollReveal/ScrollReveal')
// import sr from 'components/ScrollReveal/ScrollReveal'
// import ScrollReveal2 from 'scrollreveal'
// const ScrollReveal1 = require('scrollreveal')
// console.log(ScrollReveal1.default)
// console.log(ScrollReveal2)
export default class ListWrapper extends React.Component{
  componentDidMount(){
    const ScrollReveal = require('scrollreveal').default
    var slideRight = {
      distance: '150%',
      origin: 'right',
      opacity: null
    };

    ScrollReveal().reveal(this.refs.blogList, slideRight)
  }
  render(){
    const {children, title} = this.props
    return (
      
      <div key="amache" className="blogList" ref="blogList">
          <span>
            <article className={listWrapperStyles.listWrapper}>
              <div className={listWrapperStyles.listContent}>
                <div>{title}</div>
                {children}
              </div>
            </article>
          </span>
        </div>
    )
  }
}